import React from "react";
import { Draggable } from "react-beautiful-dnd";
import { Badge } from "./styles";
import { Text, Flex, useColorMode, Input, IconButton } from "@chakra-ui/core";
import useEditField from "components/hooks/useEditField";

interface Props {
  task?: any;
  index: number;
}

const Task: React.FC<Props> = ({ task, index }) => {
  const { colorMode } = useColorMode();
  const bgColor: any = { light: "whiteAlpha.900", dark: "gray.800" };

  const {
    field,
    isEditing,
    setIsEditing,
    setField,
    inputRef,
    handleBlur,
    handleChange,
    onKeyPressed,
  } = useEditField({
    fieldId: task?.id,
    onCreate: (field) => console.log(field),
    onEdit: (id, field) => console.log(id, field),
  });

  const renderEdittingInput = () => {
    return (
      <Input
        onBlur={handleBlur}
        ref={inputRef}
        value={field}
        onKeyPress={onKeyPressed}
        onChange={handleChange}
        variant="unstyled"
        placeholder="Task Name"
      />
    );
  };

  return (
    <Draggable draggableId={task.id} index={index}>
      {({ draggableProps, dragHandleProps, innerRef }) => (
        <Flex
          bg={bgColor[colorMode]}
          direction="column"
          p={2}
          mb={2}
          boxShadow="md"
          rounded="md"
          {...draggableProps}
          {...dragHandleProps}
          ref={innerRef}
        >
          <Flex mb={2} justify="space-between" direction="row" wrap="nowrap">
            {isEditing ? (
              renderEdittingInput()
            ) : (
              <Text fontSize="sm">{task?.content}</Text>
            )}
            <IconButton
              onClick={() => {
                setIsEditing(true);
                setField(task?.content || "");
              }}
              variant="ghost"
              aria-label="Edit Task"
              icon="edit"
              size="xs"
            />
          </Flex>
          <Flex justify="flex-start">
            <Text fontSize="10px">
              <Badge priority={task.priority}>{task.priority}</Badge>
            </Text>
          </Flex>
        </Flex>
      )}
    </Draggable>
  );
};

export default Task;
